@mixin font-connection($type, $weight, $style) {
    @font-face {
        font-family: 'WeissenhofGrotesk';
        font-weight: $weight;
        font-style: $style;
        src: url("/adpartners.git/wp-content/themes/webdesignsuntheme/assets/fonts/weissenhof-grotesk/WeissenhofGrotesk-"+$type+".woff2") format("woff2"), url("/adpartners/wp-content/themes/webdesignsuntheme/assets/fonts/weissenhof-grotesk/WeissenhofGrotesk-"+$type+".ttf") format('truetype'); } }

@include font-connection(Light, 300, normal);
@include font-connection(Regular, 400, normal);
@include font-connection(Medium, 500, normal);
@include font-connection(Bold, 700, normal);
