        justify-content: flex-end;

        & > div {
            max-width: 41em;
            margin: 0 1em;

            & > p:nth-child(1) {
                font-size: 2em;
                font-weight: 300;
                letter-spacing: .011em;
                line-height: 1.3em;
                max-width: 70%;
                margin: 1.4em 0 .8em; }

            & > p:nth-child(2) {
                margin: 0;
                max-width: 66%; }

            & > p:nth-child(3) {
                margin: 3em 0 1.4em; } } }

    & > div:last-child {
        width: 26%;
