.landing-section-5 {
    margin-top: 5.2em;

    .content-area {

        .vertical-title {
            top: 3.7em; }

        .testimonials-block {
            background: linear-gradient(137.8deg, #4A5444 0%, #343D2F 100%);
            color: #fff;
            padding: 6em 1em 5em;
            min-height: 17em;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;

            .div-triangle-up {
                position: absolute;
                bottom: 0;
                left: -1.5em; }

            .testimonials-container {

                ul {
                    margin: 0;
                    padding: 0;
                    list-style: none;
                    text-align: center;
                    font-weight: 300;

                    li {
                        display: none;

                        blockquote {
                            font-size: .778em;
                            line-height: 2.1em;
                            max-width: 43em;
                            margin: 0 auto;

                            cite {
                                display: block;
                                margin-top: 3.6em;
                                font-style: normal;
                                font-weight: inherit; } } } }

                ul > li:first-child {
                    display: block; } }

            .testimonials-bullets {

                ul {
                    list-style: none;
                    display: flex;
                    margin: 0;
                    padding: 0;
                    align-items: center;
                    justify-content: center;

                    li {
                        display: block;
                        margin: 0 .8em;
                        cursor: pointer;
                        opacity: .5;

                        img {
                            width: 2.4em;
                            height: 2.4em;
                            border-radius: 50%; } }

                    .active {
                        opacity: 1; } } } } } }

